.container
{
  width: 400px;
  height: 500px;
  margin: 0 auto;
  margin-top: 20vh;
  background-color: #fff;
  border-radius: 8px;
  -webkit-box-shadow: 0 0 48px 24px rgba(0, 0, 0, 0.07);
          box-shadow: 0 0 48px 24px rgba(0, 0, 0, 0.07);
  overflow-x: hidden;
  -webkit-animation: movingShadow ease 2s infinite alternate;
          animation: movingShadow ease 2s infinite alternate;
  position: relative;
}
.header
{
  height: 100px;
  background: -webkit-gradient(linear, left top, right bottom, from(#5f2c82), to(#49a09d));
  background: -webkit-linear-gradient(top left, #5f2c82, #49a09d);
  background: -o-linear-gradient(top left, #5f2c82, #49a09d);
  background: linear-gradient(to bottom right, #5f2c82, #49a09d);
  display: -ms-flex;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  color: #fff;
  font-size: 24px;
  letter-spacing: 0.3px;
  border-radius: 8px 8px 0 0;
  background-size: 150%;
  -webkit-animation: movingBg ease 15s infinite;
          animation: movingBg ease 15s infinite;
}
@media screen and (max-width: 1300px)
{
  .container
  {
    margin-top: 15vh;
  }
}
@media screen and (max-width: 767px)
{
  .container
  {
    width: 100%;
    height: 100%;
    margin: 0;
    border-radius: 0;
    height: 90vh;
    -webkit-box-shadow: none;
            box-shadow: none;
  }
  .header
  {
    border-radius: 0;
  }
}
.add-button
{
  width: 60px;
  height: 60px;
  background-color: #49a09d;
  color: #fff;
  font-size: 24px;
  display: -ms-flex;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  border-radius: 50%;
  position: absolute;
  right: 20px;
  bottom: 20px;
  -webkit-box-shadow: 0 0 14px 8px rgba(0, 0, 0, 0.07);
          box-shadow: 0 0 14px 8px rgba(0, 0, 0, 0.07);
  -webkit-transition: ease .6s;
  -o-transition: ease .6s;
  transition: ease .6s;
  cursor: pointer;
}
.add-button:hover
{
  -webkit-transform: scale(1.05);
      -ms-transform: scale(1.05);
          transform: scale(1.05);
}



